<template>
  <div class="search">
    <div class="top">
      <van-icon name="arrow-left" size="22" color="#ff8600" @click="onClickLeft"/>
      <van-search v-model="value" shape="round" placeholder="请输入搜索关键词" @search="onSearch" >
      </van-search>
      <van-button size="small" round color="linear-gradient(to right,#ff9000 0,#ff5000 100%)" @click="onSearch">搜索</van-button>
    </div>
    <!-- 商品 -->
    <div class="content" >
      <div class="hotBotList">
        <p>热门搜索</p>
        <div class="list">
          <span v-for="(item,i) in hotSearch" :key="i" @click="setVal(item)">{{item}}</span>
        </div>
      </div>
      <GoodsList :List="GoodsList"><div class="list_hd"><img src="https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png"></div></GoodsList>
    </div>
  </div>
</template>

<script>
import GoodsList from "../components/GoodsList"
import "../assets/style/search.less"
export default {
  name:"search",
  components:{
    GoodsList
  },
  data(){
    return {
      // 当前输入的值
      value:"",

      // 搜索出来的商品数据
      searchData:[],

      //猜你喜欢
      GoodsList:[],

      //搜索建议
      hotSearch:[
        '运动鞋',
        '香水',
        '车载电器',
        '男装',
        '女装',
        '箱包',
        '音响',
        '无人机',
        '床品件套',
        '笔记本',
        '手机'
        ]

    }
  },
  created(){
    // this.hotBot()
    // 获取猜你喜欢数据
    this.axios(this.baseUrl+"/groom/list/2?page=1&limit=30").then(data=>{
      this.GoodsList = data.data.data.list;
    })
  },
  methods:{
    //返回上一级
    onClickLeft() {
      this.$router.back();
    },

    //点击热门搜索进行搜索
    setVal(val){
      this.value = val
      this.$router.push({name:'GoodsList',params:{val:this.value}})
    },

    // 搜索商品
    onSearch(){
      if (!this.value) {
        this.$toast({
          message: "请输入搜索内容",
          forbidClick: true,
          duration: 3000,
        });
        return
      }

      this.$router.push({name:'GoodsList',params:{val:this.value}})

    },

    //History
    hotBot(){
      this.axios({
        method:"GET",
        url: this.baseUrl + "/search/keyword"
      }).then(res=>{
        console.log(res);
        if (res.data.status == 200) {
          res.data.data = this.hotSearch
          console.log(res.data.data);
        }
      })
    }
  }
}
</script>